* {
    scroll-behavior: smooth;
}

/* Dark mode */

.dark-mode {
    background-image: linear-gradient(to top, #3b41c5 0%, #a981bb 49%, #ffc8a9 100%);
    background-position: center;
    background-repeat: no-repeat;
    background-attachment: fixed;
}

/* Swtich Design */
.nav-item>.switch {
    position: relative;
    display: inline-block;
    width: 60px;
    height: 34px;
    margin-bottom: 0;
}

.nav-item>.switch input {
    opacity: 0;
    width: 0;
    height: 0;
}

.nav-item>.switch>.slider {
    position: absolute;
    cursor: pointer;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: #ccc;
    -webkit-transition: .4s;
    transition: .4s;
}

.nav-item>.switch>.slider:before {
    position: absolute;
    content: "";
    height: 26px;
    width: 26px;
    left: 4px;
    bottom: 4px;
    background-color: white;
    -webkit-transition: .4s;
    transition: .4s;
}

.nav-item>.switch>input:checked+.slider {
    background: var(--gradient-color);
    background-color: var(--primary-color2);
}


.nav-item>.switch>input:checked+.slider:before {
    transform: translateX(26px);
    -webkit-transform: translateX(26px);
    -moz-transform: translateX(26px);
    -ms-transform: translateX(26px);
    -o-transform: translateX(26px);
}

/* Rounded sliders */
.nav-item>.switch>.slider.round {
    border-radius: 34px;
}

.nav-item>.switch>.slider.round:before {
    border-radius: 50%;
}

/* End of switch design */

/* End of dark mode */


/* Browser Scrollbar */
body::-webkit-scrollbar {
    width: 12px;
